/* Font Chữ */
@font-face {
  font-family: "LilitaOneCustom";
  src: url("./assets/fonts/Lilita_One/LilitaOne-Regular.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "PoppinsCustom";
  src: url("./assets/fonts/Poppins/Poppins-Black.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "PoppinsExtraBold";
  src: url("./assets/fonts/Poppins/Poppins-ExtraBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body {
  font-family: "Poppins", Arial, sans-serif;
  background: #fff;
}
/* CSS của phần Top Header -------------------------------------------------------------------------------------------------*/
.topbar {
  width: 100vw;
  background: #323232;
  color: #fff;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  font-size: 16px;
  position: relative;
  z-index: 100;
}

.topbar-left {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: 20px;
}

.colorlib-logo {
  font-weight: bold;
  letter-spacing: -1px;
  font-size: 20px;
  color: #fff;
}

.colorlib-dot {
  color: #80e36b;
  font-size: 22px;
}

.topbar-title {
  margin-left: 21px;
  color: #bdbdbd;
  font-size: 16px;
  letter-spacing: 1px;
  background-color: #222222;
  padding: 20px 35px 20px 20px;
  font-family: "Open Sans", sans-serif;
}

.topbar-right {
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 3px;
}

.topbar-right:hover {
  background-color: #333333;
}

.topbar-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 100%;
  font-size: 24px;
  color: #bdbdbd;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

/* Hover cho tất cả các icon */
.topbar-icon:hover {
  background-color: #222222;
  color: #ffffff;
}

/* Hover cho cart khi active */
.topbar-icon.cart.active:hover {
  background-color: #222222;
  color: #fff;
}

.topbar-icon.cart.active {
  width: 60px;
  font-size: 18px;
  background: #7ac64d;
  color: #fff;
  border-radius: 0;
}

.topbar-icon.cart.active:hover {
  color: #fff;
}

/* CSS của phần Header -------------------------------------------------------------------------------------------------*/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 85.2px;
  background: transparent;
  position: relative;
  z-index: 2;
  background: linear-gradient(to right, #ebefff 50.5%, #fff 50%);
}

.logo {
  position: relative; /* Cho phép phần tử con dùng absolute dựa trên nó */
  display: flex;
  align-items: center;
  font-weight: 1000;
  font-size: 26px;
  margin-left: 12%;
}

.logo-circle {
  position: absolute;
  left: 0;
  z-index: 1;
  background: #3b5bfe;
  color: #fff;
  border-radius: 50%;
  width: 37px;
  height: 37px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  margin-left: 15px;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.logo-text {
  position: relative;
  padding-left: 24px; /* chừa chỗ cho logo-circle */
  color: #22223b;
  z-index: 2;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}
.highlightR {
  color: white;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.header nav {
  display: flex;
  gap: 36px;
  margin-right: 15vw;
}

.header nav a {
  color: #22223b;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.2s;
  font-family: "Poppins", Arial, sans-serif;
  padding: 0 0 2px;
  position: relative;
  display: inline-block;
  transition: color 0.3s ease;
  letter-spacing: 1px;
  font-weight: 400;
}
.header nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: center;
  width: 100%;
  height: 2px;
  background-color: #3b5bfe;
  transition: transform 0.3s ease;
}
.header nav a:hover::after {
  transform: translateX(-50%) scaleX(1);
}
.nav-link {
  display: block;
}

/* Phần giới thiệu nổi bật hero section---------------------------------------------------------------------------------*/
.hero {
  width: 100vw;
  min-height: 660px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: auto;
  background: linear-gradient(to right, #ebefff 50%, #fff 50%);
}

.hero-content {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 1;
  margin-bottom: 140px;
}

.hero-subtitle {
  color: #3b5bfe;
  font-weight: 700;
  letter-spacing: 4px;
  font-size: 16px;
  margin-bottom: 18px;
  margin-top: 40px;
  font-family: "PoppinsCustom", sans-serif;
}

.hero-title {
  font-size: 60px;
  font-weight: 900;
  margin: 0 0 18px 0;
  color: #111;
  line-height: 1.1;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-desc {
  font-size: 32px;
  font-weight: 700;
  color: #22223b;
  margin-bottom: 32px;
  font-family: "Poppins", Arial, sans-serif;
}

.hero-highlight {
  color: #3b5bfe;
  text-decoration: underline;
  font-weight: ̣900;
  font-size: 32px;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-arrow-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  position: relative;
  height: 70px;
}

.hero-arrow-wrapper::before {
  content: "";
  position: absolute;
  width: 68px;
  height: 68px;
  background: #ebefff;
  transform: rotate(45deg);
  z-index: 0;
}

.hero-arrow {
  position: relative;
  font-size: 30px;
  color: #3b5bfe;
  z-index: 1;
  animation: arrowDown 2.3s infinite;
  user-select: none;
  line-height: 1;
}


@keyframes arrowDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(12px);
  }
  100% {
    transform: translateY(0);
  }
} /*Hiệu ứng này em tham khảo ở https://tenten.vn/tin-tuc/transform-css-la-gi/*/

/* About Section */
.about {
  width: 100%;
  background: #fff;
  padding: 0;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
  margin-right: 7vw;
}

.about-row {
  display: flex;
  align-items: flex-start;
  background: #f8f9fa;
  box-shadow: 0 10px 40px rgba(60, 72, 88, 0.07);
  overflow: hidden;
}

.about-left {
  flex: 1;
  background: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 500px;
}

.about-img {
  width: 555px;
  height: 683px;
  object-fit: cover;
  box-shadow: 0 10px 40px rgba(60, 72, 88, 0.13);
}

.about-right {
  flex: 1.2;
  padding: 60px 60px 30.5px 60px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-title {
  font-size: 42px;
  font-weight: 800;
  margin-bottom: 18px;
  color: #111;
  letter-spacing: -1px;
}

.about-desc {
  color: #8b8b99;
  font-size: 18px;
  margin-bottom: 32px;
  line-height: 1.7;
}

.about-info {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.about-info li {
  margin-bottom: 12px;
  font-size: 17px;
  color: #222;
  display: flex;
  line-height: 1.5;
}

.about-info span {
  font-weight: 700;
  min-width: 140px;
  color: #111;
  margin-right: 10px;
  display: inline-block;
}

.about-projects {
  font-size: 20px;
  margin-bottom: 32px;
  color: #222;
}

.about-number {
  color: #4ea8de;
  font-weight: 700;
  font-size: 22px;
  margin-right: 4px;
}

.about-btn {
  display: inline-block;
  background: #4ea8de;
  color: #fff;
  padding: 14px 36px;
  border-radius: 28px;
  font-weight: 700;
  font-size: 16px;
  text-decoration: none;
  letter-spacing: 1px;
  transition: background 0.2s;
  border: none;
  margin-top: 10px;
  box-shadow: 0 4px 18px rgba(78, 168, 222, 0.08);
}

.about-btn:hover {
  background: #22223b;
}

@media (max-width: 992px) {
  .about-row {
    flex-direction: column;
    border-radius: 0;
  }
  .about-left, .about-right {
    padding: 32px 16px;
    min-height: unset;
  }
  .about-img {
    width: 220px;
    height: 290px;
  }
  .about-right {
    padding: 32px 16px;
  }
}
/*Phần hình ảnh*/
.partner-section {
  width: 100%;
  padding: 0;
}

.partner-container {
  max-width: 1520px;
  margin: 0 auto;
  background: #FFFFFF;
  padding: 32px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 64px;
}

.partner-logo {
  height: 65px;
  object-fit: contain;
}
/* Responsive */
@media (max-width: 900px) {
  .about-row {
    flex-direction: column;
    min-height: unset;
  }
  .about-left,
  .about-right {
    flex: unset;
    width: 100%;
    min-height: unset;
    padding: 30px 15px;
  }
  .about-right {
    padding: 30px 15px;
  }
  .about-img {
    max-width: 260px;
  }
  .about-title {
    font-size: 32px;
  }
}
/*Phần khoảng trắng*/
.resume-section{
  width: 100%;
  height: 4397.2px;
}
.col-aside{
  width: 285px;
  height: 100%;
}
/* Phần Research Responsive*/
@media (max-width: 768px) {
  .my-component {
    transform: scale(0.5);
    transform-origin: top left; /* giúp giữ vị trí ở góc */
  }
}
